import {useState} from 'react';
import './App.css';
import Navbar from './Components/Navbar';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import StarRateIcon from '@material-ui/icons/StarRate';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import DeleteIcon from '@material-ui/icons/Delete';

const useStyles = makeStyles((theme) => ({
  button: {
    margin: theme.spacing(1),
  },
}));

function App() {

  //This is products array i set some default objects to array
  const [products, setProducts] = useState([
    {
      id: 1,
      name: 'Itachi Product',
      image: 'data:image/jpeg;base64,',
      fixedBill: '$40.00 - $80.00',
      discountBill: undefined,
      addCart: false
    },
    {
      id: 2,
      name: 'Joy Stick',
      image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQbyI5gGx7IWyiia6HinEJ1COGCLUaAzHWsQA&usqp=CAU',
      fixedBill: '$80.00',
      discountBill: '$50.00',
      addCart: false
    },{
      id: 3,
      name: 'PlayStation 4',
      image: 'https://media.gamestop.com/i/gamestop/B909276M/PlayStation-4-and-Shadow-of-the-Tomb-Raider-System-Bundle-GameStop-Premium-Refurbished?w=190&h=190&fmt=webp',
      fixedBill: '$299.99',
      discountBill: '$249.99',
      addCart: false
    },{
      id: 4,
      name: 'PlayStation 5',
      image: 'https://media.gamestop.com/i/gamestop/11108140/PlayStation-5?$pdp$&fmt=webp',
      fixedBill: '$499.99',
      discountBill: undefined,
      addCart: false
    },{
      id: 5,
      name: 'iPhone 12 Pro',
      image: 'https://www.reliancedigital.in/medias/Apple-12-Pro-Smartphones-491901558-i-1-1200Wx1200H?context=bWFzdGVyfGltYWdlc3wxMTAwNjV8aW1hZ2UvanBlZ3xpbWFnZXMvaDRhL2gxNS85NDA3ODAzMjYwOTU4LmpwZ3wxMmM0ZTFkZDcwZDc3ZTk0MmQxNjNhYjkzMzNiYWRjNWU2OTIxNzZmZWVmZGRhMTZjYTQ3MzUyMmU2NGM4Zjg1',
      fixedBill: '$1091.99',
      discountBill: undefined,
      addCart: false
    }
  ])

//Here is i store carts array and i set it default empty-array
const [carts, setCarts] = useState([]);

//Home Page Component
function HomePage() {

  //onClick function when add cart is pressed
  const onAddCart = (id) => {
    const foundProduct = products.find((product) => product.id === id);
    console.log(foundProduct);
    // Disable add to cart button when added to card
    // Im disabling when we click add cart changing boolean now im changing to true
    foundProduct.addCart = true;
    const modifyProducts = products.map((product) => {
      if(product.id === foundProduct.id){
        return foundProduct;
      }
      return product;
    })
    setProducts(modifyProducts);
    setCarts([...carts, foundProduct]);
  }

    return(
      <div>
        <header className="bg-dark py-5">
            <div className="container px-4 px-lg-5 my-5">
                <div className="text-center text-white">
                    <h1 className="display-4 fw-bolder">Shop in style</h1>
                    <p className="lead fw-normal text-white-50 mb-0">With this shop hompeage template</p>
                </div>
            </div>
        </header>
        <section className="py-5">
          <div className="container px-4 px-lg-5 mt-5">
            <div className="row gx-4 gx-lg-5 row-cols-2 row-cols-md-3 row-cols-xl-4 justify-content-center">
              {products.map((product, index) => (
                <div className="col mb-5" key={index}>
                  <div className="card h-100">
                    <div class="badge bg-dark text-white position-absolute" style={{top: '0.5rem', right: '0.5rem'}}>sale</div>
                    <img className="card-img-top" src={product.image} alt={product.name} />
                    <div className="card-body p-4">
                        <div className="text-center">
                        
                            <h5 className="fw-bolder">{product.name}</h5>
                            {/* Rating according to value give */}
                            {/* {i give the rating when its have discount Product most of people buy cheep and best} */}
                            {product.discountBill !== undefined && (<div className="d-flex justify-content-center small text-warning mb-2">
                              <div className="bi-star-fill"><StarRateIcon /></div>
                              <div className="bi-star-fill"><StarRateIcon /></div>
                              <div className="bi-star-fill"><StarRateIcon /></div>
                              <div className="bi-star-fill"><StarRateIcon /></div>
                              <div className="bi-star-fill"><StarRateIcon /></div>
                            </div>)}
                            
                            {product.discountBill ? (<div><span className="text-muted text-decoration-line-through">{product.fixedBill}</span>{product.discountBill}</div>) 
                              : <div>{product.fixedBill}</div>
                            }
                            
                        </div>
                    </div>
                    <div className="card-footer p-4 pt-0 border-top-0 bg-transparent">
                        <div className="text-center">
                          <button onClick={() => {onAddCart(product.id)}} disabled={product.addCart} className="btn btn-outline-dark mt-auto" >Add to cart</button>
                        </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </section>
      </div>
    )
}

function Cart() {

  const classes = useStyles();

  //onClick function for remove cart function
  // Remove item from cart & enable add to cart
  const onDelete = (id) => {
    const foundProduct = carts.find((product) => product.id === id);
    // Disable add to cart button when added to card
    // Im disabling when we click add cart changing boolean now im changing to flase again
    foundProduct.addCart = false;
    const modifiyProducts = products.map((product) => {
      if(product.id === foundProduct.id){
        return foundProduct
      }
      return product
    })
    setProducts(modifiyProducts);
    const modifiedCart = carts.filter((cart) => cart.id !== id);
    setCarts(modifiedCart);
  }

  return (
    <div>
        <header className="bg-dark py-5">
            <div className="container px-4 px-lg-5 my-5">
                <div className="text-center text-white">
                    <h1 className="display-4 fw-bolder">Shop in style</h1>
                    <p className="lead fw-normal text-white-50 mb-0">With this shop hompeage template</p>
                </div>
            </div>
        </header>
        <section className="py-5">
          <div className="container px-4 px-lg-5 mt-5">
            <div className="row gx-4 gx-lg-5 row-cols-2 row-cols-md-3 row-cols-xl-4 justify-content-center">
              {carts.map((product, index) => (
                <div className="col mb-5" key={index}>
                  <div className="card h-100">
                    <div class="badge bg-dark text-white position-absolute" style={{top: '0.5rem', right: '0.5rem'}}>sale</div>
                    <img className="card-img-top" src={product.image} alt={product.name} />
                    <div className="card-body p-4">
                        <div className="text-center">
                        
                            <h5 className="fw-bolder">{product.name}</h5>
                            {product.discountBill !== undefined && (<div className="d-flex justify-content-center small text-warning mb-2">
                              <div className="bi-star-fill"><StarRateIcon /></div>
                              <div className="bi-star-fill"><StarRateIcon /></div>
                              <div className="bi-star-fill"><StarRateIcon /></div>
                              <div className="bi-star-fill"><StarRateIcon /></div>
                              <div className="bi-star-fill"><StarRateIcon /></div>
                            </div>)}
                            
                            {product.discountBill ? (<div><span className="text-muted text-decoration-line-through">{product.fixedBill}</span>{product.discountBill}</div>) 
                              : <div>{product.fixedBill}</div>
                            }
                            
                        </div>
                    </div>
                    <div className="card-footer p-4 pt-0 border-top-0 bg-transparent">
                      <div className="text-center">
                      <Button
                        variant="contained"
                        color="primary"
                        className={classes.button}
                        startIcon={<DeleteIcon />}
                        onClick={() => {onDelete(product.id)}}
                      >
                        Remove Cart
                      </Button>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </section>
      </div>
  );
}

//Ruting fro components
  return (
    <div className="App">
     <Router>
       <Navbar carts={carts} />
       <Switch>
        <Route exact path="/">
          <HomePage />
        </Route>
        <Route exact path="/cart">
          <Cart />
        </Route>
       </Switch>
     </Router>
    </div>
  );
}

export default App;
